$h2-before-width: 4px;
h2 {
    position: relative;
    margin-bottom: 12px;
    text-indent: $h2-before-width * 2;
    &::before {
        content: "";
        position: absolute;
        top: $h2-before-height / 2;
        left: 0;
        width: $h2-before-width;
        height: calc(100% - #{$h2-before-height});
        background: #333;
    }
}
.ve-container {
    position: relative;
    // width: calc(100% - 40px);
    width: 700px;
    margin: 60px auto 0;
    .ve-button-area {
        margin: 10px 0 0 0;
        .ve-button {
        padding: 4px 10px;
        background: #00a3cf;
        border: 0;
        border-radius: 3px;
        color: #fff;
        cursor: pointer;
        }
    }
}



